.Board {
	width: 350px;
	height: 420px;
	display: grid;
	grid-template-rows: repeat(6, 1fr);
	grid-gap: 5px;
	padding: 10px;
	box-sizing: border-box;
}
.board-container {
	display: flex;
	justify-content: center;
	align-items: center;
	flex-grow: 1;
	min-height: var(--game-min-height);
}
.submit-btn-container {
	margin: 20px 0;
	display: flex;
	justify-content: center;
	align-items: center;
}
.submit-btn {
	width: 75px;
	height: 50px;
	border: 1px solid white;
}

@media (max-width: 380px) {
	.Board {
		width: 305px;
		height: 360px;
	}
}